@font-face {
  font-family: 'MontserratRegular';
  src: url("../fonts/Montserrat-Regular.woff2");
}
@font-face {
  font-family: 'MontserratSemiBold';
  src: url("../fonts/Montserrat-SemiBold.woff2");
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'MontserratSemiBold';
  font-weight: 600;
  line-height: 1.125;
}

h1 {
  font-size: 2em;
  margin-bottom: 0.5em;
}
h2 {
  font-size: 1.75em;
  margin-bottom: 0.5714em;
}
h3 {
  font-size: 1.5em;
  margin-bottom: 0.6666em;
}
h4 {
  font-size: 1.25em;
  margin-bottom: 0.8em;
}
h5 {
  font-size: 1.125em;
  margin-bottom: 0.8888em;
}
h6 {
  font-size: 1em;
  margin-bottom: 1em;
}

a {
  color: $primary;
  &:hover,
  &:focus {
    color: $secondary;
  }
}

strong {
  font-family: 'MontserratSemiBold';
  font-weight: 600;
}
